<template>
<div id="notification_container">
    <transition-group name="bounce" tag="div" >
        <div class="notification_body" :class="classObject" v-for="notification in listOfNotifications" :key="notification.id">
            <div class="imgContainer">
                <img src="../assets/notifications/check.svg" v-if="notification.type === 0">
                <img src="../assets/notifications/close.svg" v-if="notification.type === 1">
                <img src="../assets/notifications/warning.svg" v-if="notification.type === 2">
                <img src="../assets/notifications/info.svg" v-if="notification.type === 3">
            </div>
            <div class="contentContainer">
                <h3 class="content">{{notification.message}}</h3>
            </div>
        </div>
    </transition-group>
</div>
</template>

<script>
import {Howl, Howler} from 'howler';

export default {
    
computed: {
    classObject: function () {
        return {
            success: this.notification.type === 0,
            error: this.notification.type === 1,
            warning: this.notification.type === 2,
            info: this.notification.type === 3,
        }    
    }
},
data() {
    return {
        notification: {},
        listOfNotifications: [],
    }
},
methods: {
    newNotification: function (data) {
        const id = Math.random(1,2)
        const OBJ = {
            type: data.type,
            message: data.text, 
            time: data.time
        }
        OBJ.id = id   
        this.notification = OBJ
    }
},
watch: {
    'notification': function(val) {
        this.listOfNotifications.push(val);
        setTimeout(() => {
            for (let i = 0; i < this.listOfNotifications.length; i++) {
                if(this.listOfNotifications[i].id === val.id) {
                    this.$delete(this.listOfNotifications, i)
                }  
            }
        }, val.time * 1000);
    }
},
mounted() {
    if('alt' in window) {
      alt.on('base:notification', this.newNotification)
    }
},

}
</script>

<style scoped>

.success {
    background-color: rgba(119, 211, 119, 0.985);
    border: 1px solid green;
    border-left: 5px solid rgb(30, 141, 30);
}

.error {
    background-color: rgba(211, 133, 119, 0.985);
    border: 1px solid rgb(128, 0, 0);
    border-left: 5px solid rgb(141, 30, 30);
}

.warning {
    background-color: rgba(252, 246, 189, 0.985);
    border: 1px solid #ffd500;
    border-left: 5px solid #ffd500;
}

.info {
    background-color: rgba(127, 206, 215, 0.985);
    border: 1px solid #007ea7;
    border-left: 5px solid #007ea7;
}

#notification_container {
    position: absolute;
    right: 2%;
    top: 5%;
    width: 30%;
    height: 40%;
    transition: 2s;
}
div {
    height: 100%;
}
.notification_body {
    position: relative;
    text-align: right;
    float: right;
    top: 5%;
    height: auto;
    width: auto;
    margin-bottom: 1%;
    padding-left: 10%;
    padding-right: 5%;
    clear: both;
}
.notification_body .imgContainer {
    position: relative;
    float: left;
    top: .7rem;
    left: -2.7rem;
}

.notification_body .imgContainer img {
    position: relative;
    margin: 0%;
    padding: 2%;
    height: 2rem;
    width: auto;
}

.notification_body .contentContainer {
    position: relative;
    height: 100%;
    width: 70%;
    top: 0%;
    left: 0%;
    width: auto;
}

.notification_body .contentContainer .content {
    position: relative;
    font-size: .7vw;
    font-weight: 600;
    color: rgb(44, 44, 44);
    font-family: 'Open Sans', sans-serif;
    bottom: 1.3rem;
    left: 2%;
    text-align: left;
    float: left;
}

.bounce-enter-active {
  animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
  0% {
      opacity: 0;
    transform: translateY(-100%);
  }
  30% {
      opacity: 0;
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

</style>