<template>
  <div id="app" v-if="pageLoaded">

    <h1 class="watermarker" @mouseover="enablesound" >{{locale.SERVERNAME}}</h1>
    <speedometer 
      v-if="speedometerState"
      :data="speedometerOBJ"
    />
    <notification 
      :notification="notificationOBJ"
    />
    <phone 
    v-if="phoneState"
    />

  </div>
</template>

<script>
import locale from './util/locale.js';
//MODULES
import speedometer from './pages/speedometer.vue';
import notification from './pages/notification.vue';
import phone from './pages/phone.vue';
//import audioPlay from './pages/audioPlay.vue'

export default {
  name: 'App',
  components: {
   speedometer,
   notification,
   phone
  },
  data() {
    return {
      locale: locale,
      pageLoaded: true,

      speedometerState: false,
      phoneState: false,
      notificationOBJ: {},

      //DATA
      speedometerOBJ: {
        speed: 0,
        distanceTraveled: 278,
        fuel: 0,
      }
    }
  },
  methods: {
    newSpeedometerData: function(data) {
      this.speedometerOBJ = data;
    },

    startWebview: function () {
      this.pageLoaded = true;
    },

    toggleSpeedMeter: function (typeOfPage) {
      this.speedometerState = typeOfPage;
    },

    enablesound: function () {
      var context = new AudioContext();

      context.loadSound('./assets/audio/success.ogg', 'success');
      console.log("SOUND")
      context.playSound('success');
    }
  },
  mounted() {
    
    if('alt' in window) {
      setTimeout(() => {
        alt.emit('base:ready');
      }, 50);
      alt.on('base:toggleSpeedMeter', this.toggleSpeedMeter);
      alt.on('base:start', this.startWebview);
      alt.on('base:speedometerData', this.newSpeedometerData);
    }
  },

}
</script>

